import React,{Component} from 'react'
import { hashHistory } from 'react-router';
import { message, Form, Input, Spin, Button } from 'antd';
const FormItem = Form.Item;
const { TextArea } = Input;

class DataForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            loading: true,
            info: "",
            culture: ""
        };
        ['handleSubmit', 'resetForm'].forEach(m=>this[m] = this[m].bind(this))
    }

    componentDidMount() {
        let that = this;
        ajax({
            url: "/platform/company/getInfoDetail",
            callback: function(data) {
                that.setState({ loading: false});
                if(data.status == 200) {
                    that.infoKE = K.create('#info', {
                        allowFileManager : true,
                        afterChange: function() {
                            that.props.form.setFieldsValue({"info" :this.html()});
                        }
                    });
                    that.setState({
                        info: data.data.detailInfo.info,
                        culture: data.data.detailInfo.culture
                    });
                    that.infoKE.html(that.state.info);
                    that.cultureKE = K.create('#culture', {
                        allowFileManager : true,
                        afterChange: function() {
                            that.props.form.setFieldsValue({"culture" :this.html()});
                        }
                    });
                    that.cultureKE.html(that.state.info);
                } else {
                    message.error(data.errMsg, 1);
                }
            }
        })
    }

    handleSubmit(e) {
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if (!err) {
                ajax({
                    url: "/platform/company/updateInfo",
                    data: values,
                    callback: function(data) {
                        if(data.status == 200) {
                            message.success("设置成功!", 1)
                        } else {
                            message.error(data.errMsg, 1)
                        }
                    }
                });
            }
        })
    }

    resetForm() {
        this.props.form.resetFields();
        this.infoKE.html(this.props.form.getFieldValue("info"));
        this.cultureKE.html(this.props.form.getFieldValue("culture"));
    }

    render() {
        const { getFieldDecorator } = this.props.form;
        const formItemLayout = {
            labelCol: { span: 4 },
            wrapperCol: { span: 10 }
        };
        return (
            <Spin spinning={this.state.loading} tip="Loading...">
                <Form>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="公司概况"
                        hasFeedback
                    >
                        {getFieldDecorator('info', {
                            initialValue: this.state.info
                        })(
                            <TextArea placeholder="请输入公司概况" autosize={{ minRows: 6 }} />
                        )}
                    </FormItem>
                    <FormItem
                        required
                        {...formItemLayout}
                        label="企业文化"
                        hasFeedback
                    >
                        {getFieldDecorator('culture', {
                            initialValue: this.state.culture
                        })(
                            <TextArea placeholder="请输入企业文化" autosize={{ minRows: 6 }} />
                        )}
                    </FormItem>
                    <FormItem
                        wrapperCol={{ span: 12, offset: 6 }}
                    >
                        <Button type="primary" onClick={this.handleSubmit} style={{"marginRight": "20px"}}>提交</Button>
                        <Button type="danger" onClick={this.resetForm}>重置</Button>
                    </FormItem>
                </Form>
            </Spin>
        )
    }
}

const WrappedDataForm = Form.create({})(DataForm);

module.exports = WrappedDataForm;


